'use strict';
angular.module('videoHub')
  .controller('VideoListController', function($scope, $location, $routeParams, $rootScope, ListService) {
    $scope.orderBy = $location.search()['order_by'];
    $scope.filter = $location.search().f;
    $scope.search = $location.search().q;
    ListService.setService('VideoService');
    function updateList(){
      ListService.updateList()
        .then(function(){
          $scope.list = ListService.data.list;
          $scope.totalItems = ListService.data.totalItems;
        });
    }
    updateList();

    $scope.setFilterAndUpdateList = function(newFilterValue){
      //Only used to determine active state
      $scope.filter = newFilterValue;
      $location.search('f', newFilterValue);
      updateList();
    };

    $scope.changeToEditPage = function(id){
      $location.path('cms/edit/' + id + '/');
    };

    $scope.setOrderAndUpdateList = function(newOrderValue){
      if ($scope.orderBy && ($scope.orderBy.indexOf(newOrderValue) > -1 && $scope.orderBy.indexOf('-') === -1)){
        $scope.orderBy = '-' + newOrderValue;
      } else {
        $scope.orderBy = newOrderValue;
      }
      $location.search('order_by', $scope.orderBy);
      updateList();
    };

    $scope.clearSearchAndUpdateList = function(){
      $scope.search = '';
      $location.search('q', null);
      updateList();
    };

    $scope.pageChanged = function(newPageNumber) {
      $location.search('page', newPageNumber);
      updateList();
    };

    $scope.setSearchAndUpdatePage = function(){
      $location.search('q', $scope.search);
      updateList();
    };

    $scope.new = function() {
      $location.path('cms/new/');
    };
  });
