'use strict';
angular.module('videoHub')
  .service('ListService', function($rootScope, $location, $injector, ValidQueryParamsForListPage) {
    this.data = {};
    var ListService = this;
    this.service = null;
    var currentService = null;

    this.setService = function(serviceName){
      this.service = $injector.get(serviceName);
      currentService = this.service;
      this.data.list = [];
      this.data.totalItems = [];
    };

    this.setList = function(list) {
      ListService.data.list = list;
      ListService.data.totalItems = list.count;
    };

    this.updateList = function() {
      var params = $location.search();
      params = _.pick(params, ValidQueryParamsForListPage);
      return currentService.getList(params).then(ListService.setList);
    };
  });
