'use strict';

angular.module('videoHub')
  .constant('AlertServiceConstants', {
    TIME_TO_WAIT_BEFORE_CLEARING: 2000
  })
  .service('AlertService', function($timeout, $q, AlertServiceConstants) {
    this.alertStatus = {};
    this.clear = function(clearAfterDisplayingMessageFlag) {
      if (_.isUndefined(clearAfterDisplayingMessageFlag)){
        this.setAlertStatusElementsToFalse();
      }
      var alertDeferred = $q.defer();
      var alertService = this;
      if (clearAfterDisplayingMessageFlag) {
        $timeout(function() {
          alertService.setAlertStatusElementsToFalse();
          alertDeferred.resolve();
        }, AlertServiceConstants.TIME_TO_WAIT_BEFORE_CLEARING);
      } else {
        alertDeferred.resolve();
      }

      return alertDeferred.promise;
    };

    this.setAlertStatusElementsToFalse = function(){
      this.alertStatus.error = false;
      this.alertStatus.info = false;
      this.alertStatus.success = false;
    };

    this.success = function(message, clearAfterDisplayingMessageFlag) {
      this.clear();
      this.alertStatus.success = message;
      return this.clear(clearAfterDisplayingMessageFlag);
    };
    this.error = function(message, clearAfterDisplayingMessageFlag) {
      this.clear();
      this.alertStatus.error = message;
      return this.clear(clearAfterDisplayingMessageFlag);
    };
    this.info = function(message, clearAfterDisplayingMessageFlag) {
      this.clear();
      this.alertStatus.info = message;
      return this.clear(clearAfterDisplayingMessageFlag);
    };
  });
