'use strict';
angular.module('videoHub')
  .constant('DatetimePickerConstants', {
    'DATETIME_PICKER_FORMAT': 'Y-m-d h:i A',
    'MOMENTJS_FORMAT': 'YYYY-MM-DDTHH:mm:DDZZ'
  })
  .directive('datetimepicker', ['$filter', 'DatetimePickerConstants', function($filter, DatetimePickerConstants) {
    return {
      scope: {
        'date': '='
      },
      replace: true,
      require:'ngModel',
      template: '<input class="form-control" ng-model="date" type="text"/>',
      link: function(scope, element, attrs, ngModel){
        scope.required = attrs.required || false;
        element.datetimepicker({
          format: DatetimePickerConstants.DATETIME_PICKER_FORMAT,
          startDate: new Date(),
          step: 15
          });

        function convertToLocal(value){
          return $filter('convertToLocal')(value);
        }

        ngModel.$formatters.unshift(convertToLocal);

        scope.formatDateForBackend = function(value){
          if (value){
            return moment(value, DatetimePickerConstants.DATETIME_PICKER_FORMAT).format(DatetimePickerConstants.MOMENTJS_FORMAT);
          }
        };

        ngModel.$parsers.unshift(scope.formatDateForBackend);
      }
    };
  }]);
