'use strict';
angular.module('videoHub')
  .constant('ChannelDetailControllerConstants', {
    SAVED_ALERT_MESSAGE: 'Saved',
    SOMETHING_WENT_WRONG_ALERT_MESSAGE: 'Something went wrong with saving the Channel. Please call your friendly sysadmin.',
    PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE: 'Please fill in all required fields.'
  })
  .controller('ChannelDetailController', function($scope, $routeParams, ChannelService, $location, AlertService, ChannelDetailControllerConstants) {
    $scope.channel = {};

    $scope.getChannel = function() {
      if (_.isUndefined($routeParams.channelId)) {
        $scope.channel = {};
      } else {
        return ChannelService.one($routeParams.channelId).get().then(
          function(data) {
            $scope.channel = data;
          },
          function(data) {
            console.log(data);
          }
        );
      }
    };

    $scope.saveChannel = function() {
      if ($scope.channelDetailForm.$valid) {
        if ($routeParams.channelId) {
          $scope.channel.save().then(displaySaveSuccessfulAlert, displaySomethingWentWrongAlert);
        } else {
          ChannelService.post($scope.channel).then(navigateToNewlyCreatedEditPage, displaySomethingWentWrongAlert);
        }
      } else {
        AlertService.error(ChannelDetailControllerConstants.PLEASE_FILL_IN_ALL_REQUIRED_FIELDS_ALERT_MESSAGE, true);
      }
    };

    function navigateToNewlyCreatedEditPage(response) {
      displaySaveSuccessfulAlert().then(function() {
        $location.path('cms/edit/' + response.id + '/');
      });
    }

    function displaySaveSuccessfulAlert() {
      return AlertService.success(ChannelDetailControllerConstants.SAVED_ALERT_MESSAGE, true);
    }

    function displaySomethingWentWrongAlert() {
      AlertService.error(ChannelDetailControllerConstants.SOMETHING_WENT_WRONG_ALERT_MESSAGE, true);
    }
    $scope.getChannel();
  });
