'use strict';

angular.module('videoHub')
  .directive('deliveryChart', function() {
    return {
      restrict: 'E',
      templateUrl: 'shared/chart/chart.html',
      scope: {
        impressions: '@impressions'
      },
      //Setting up Highcharts options
      controller: function($scope) {
        $scope.semiCircleConfig = {
          options: {
            chart: {
              type: 'pie',
              backgroundColor: 'transparent',
              height: 75,
              margin: 0,
              width: 75,
            },
            exporting: { enabled: false },
            credits: { enabled: false },
            tooltip: { enabled: false },
            colors: ['#008d52', '#e2e2e2'],
          },
          series: [{
            dataLabels: { enabled: false },
            states: {
              hover: {
                 enabled: false
              }
            },
            animation: false,
            startAngle: -90,
            endAngle: 90,
            center: ['50%', '75%'],
            innerSize: '50%',
            name: 'Delivery',
            data: [
              parseInt($scope.impressions),
              (100 - parseInt($scope.impressions))
            ]
          }],
          title: {
            text: ''
          },
          subtitle: {
            text: ''
          },
          loading: false
        };
      }
    };
  });
