'use strict';

angular.module('videoHub')
  .constant('VideoFieldConstants', {
    FILE_TOO_LARGE_ERROR_MESSAGE: 'Upload file cannot be larger than 1024MB.',
    MUST_UPLOAD_FILES_ERROR_MESSAGE: 'You must upload a video file.',
    PLEASE_SELECT_A_FILE_ERROR_MESSAGE: 'Please select a file.',
    UPLOAD_COMPLETE_SUCCESS_MESSAGE: 'Upload Complete'
  })
  .directive('videoField', function(Zencoder, $q) {
    return {
      templateUrl: 'components/videodetail/videofield.html',
      transclude: true,
      controller: function($scope, AlertService, $timeout, VideoFieldConstants, VideoService) {

        $scope.uploadVideo = function() {
          var fileField = $('#' + $scope.video.id + '-file-field');
          fileField.unbind('change');
          fileField.bind('change', function(event) {
            var file = event.target.files[0];
            $scope.validateVideoFile(file)
              .then($scope.addVideo)
              .then(Zencoder.uploadToS3AndEncode)
              .then(
                function() {
                  AlertService.success(VideoFieldConstants.UPLOAD_COMPLETE_SUCCESS_MESSAGE).then($scope.fetchNewVideoObject);
                },
                function(error) {
                  AlertService.error(error, true);
                },
                function(message) {
                  if (!_.isUndefined(message)) {
                    AlertService.info(message);
                  }
                });
          });
          fileField.click();
        };

        $scope.validateVideoFile = function(file) {
          var validateVideoFileDeferred = $q.defer();
          if (file) {
            if (file.size > (1024 * 1024 * 1024)) {
              validateVideoFileDeferred.reject(VideoFieldConstants.FILE_TOO_LARGE_ERROR_MESSAGE);
            } else if (file.type.indexOf('video/') !== 0) {
              validateVideoFileDeferred.reject(VideoFieldConstants.MUST_UPLOAD_FILES_ERROR_MESSAGE);
            } else {
              validateVideoFileDeferred.resolve(file);
            }
          } else {
            validateVideoFileDeferred.reject(VideoFieldConstants.PLEASE_SELECT_A_FILE_ERROR_MESSAGE);
          }
          return validateVideoFileDeferred.promise;
        };

        $scope.addVideo = function(file) {
          var addVideoDeferred = $q.defer();
          $scope.video.save().then(function() {
            var uploadToS3Arguments = {
              'file': file,
              'videoObject': $scope.video
            };
            addVideoDeferred.resolve(uploadToS3Arguments);
          });
          return addVideoDeferred.promise;
        };

        function checkIfSourcesHaveLoadedAndRetryIfTheyAreNot(response) {
          if (_.isEmpty(response.data.sources)) {
            $timeout($scope.fetchNewVideoObject, 2000);
          } else {
            $scope.video = response.data;
            AlertService.clear();
          }
        }

        $scope.fetchNewVideoObject = function() {
          // We reload the video after all of the fun is over, in order to update it with the new sources.
          VideoService.one($scope.video.id).get()
            .then(checkIfSourcesHaveLoadedAndRetryIfTheyAreNot);
        };
      }
    };
  });
